/*Desktop view above screensize of 1200 width*/

.Cards__heading{
    font-weight: bold;
    color: #000000;
    font-size: 42px;
    font-style: normal;
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    padding-left: 102px;
    padding-top: 3rem;
    margin-bottom: 40px;
}
.Cards__svg1{
    margin-left: 1175px !important;
}
.Cards__LeftNames{
    position:absolute;
    top:0;
    margin-left: 232px;
}
.Cards__LeftDoctors{
    width: 111px;
    height: 111px;
    margin-left:100px;
    border: 4px solid #E82D7C;
    border-radius: 48px;
    padding: 5px 4px 6px 7px;
    position: relative;
}
.Cards__RightNames{
    position:absolute;
    top:0;
    margin-left: 204px;
}
.Cards__RightDoctors{
    width: 111px;
    height: 111px;
    margin-left: 72px !important; 
    border: 4px solid #E82D7C;
    border-radius: 48px;
    padding: 5px 4px 6px 7px;
    position: relative;
}
.Cards__name{
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 21px;
    color: #000000;
    width: 241px;
}
.Cards__post{
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    align-items: center;
    display: flex;
    color: #000000;
    width: 220px;
    margin-top: -10px;
}
.Cards__address{
    width: 96px;
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
    color: #000000;
    margin-top:-10px;
}
.btn{
    color:#1A936F;
    line-height: 21px;
    font-size:18px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: bold;
    box-sizing: border-box;
    border: 2px solid #1A936F;
    border-radius: 10px;
    width: 135px;
    height: 33px;
    background-color: #ffffff;
    margin-bottom: 35px;
    padding: 6px 0px 6px 0px !important;
}
.btn:hover{
    color:#1A936F;
    border: 2px solid #1A936F;
    background-color: #ffffff;
}
.Cards__Leftbutton{
    position:absolute;
    top:0;
    margin-left:512px;
}
.Cards__button{
    position:absolute;
    top:0;
    margin-left: 484px;
}
